                    <div style="padding-bottom: 0px">
                      <input type="text" ref="idBox" name="user_id" placeholder="" @keyup="idBoxNullCheck"
                             @keydown.enter="sameIdCheck" v-model="id" class="PersonUnder data_insert_box"/>
                      <button type="button" @click="sameIdCheck" :disabled="idBoxCheck" class="btn bg-gradient-dark" style="margin-top: 5px"></button>
                      <span :hidden="msg === 'id   '" style="margin-left: 10px">{{ msg }}</span>
                    </div>
                    <div style="padding-top: 0px">
                      <input type="password" class="data_insert_box" ref="pwBox" name="user_pw" autoComplete="off"
                             placeholder="(8~20 ,,)" v-model="password"/>
                    </div>
                    <div>
                      <input type="email" class="data_insert_box" ref="emailBox" name="user_email" placeholder=""
                             v-model="email"/>
                    </div>
                    <div style="padding-bottom: 0px">
                      <input type="text" ref="phNumBox" name="user_phone" placeholder=" -" v-model="phoneNum"
                             class="PersonUnder data_insert_box"/>
                      <button type="button" class="btn bg-gradient-dark" style="margin-top: 5px"> </button>
                    </div>
                    <div style="padding: 0px 5px">
                      <input type="text" ref="certifNumBox" name="" placeholder=" " v-model="certification_num"
