                      <button type="button" class="btn bg-gradient-dark" id="rightBtn" style="margin-top: 5px; margin-left: 5px"></button>
                    </div>
                  </div>

                  <div class="CompanyBox" :class="{ 'PeopleForm' : hasError, active : isActive }">
                    <div>
                      <div class="MemberInfo companyContent" :class="class2" v-if="show">
                        <div style="padding-bottom: 0px">
                          <input type="text" class="data_insert_box" placeholder=""/>
                        </div>
                        <div>
                          <input type="tel" class="data_insert_box" placeholder=" "/>
                        </div>
                        <div>
                          <input type="email" class="data_insert_box" placeholder=" "/>
                        </div>
                        <div>
                          <input type="text" class="data_insert_box" placeholder=" "/>
                        </div>
                        <div>
                          <input type="text" class="data_insert_box" placeholder=""/>
                        </div>
                        <div>
                          <input type="text" class="data_insert_box" placeholder=""/>
                        </div>
                        <div>
                          <input type="text" class="data_insert_box" placeholder=""/>
                        </div>
                        <div>
                          <input type="date" class="data_insert_box" placeholder="">
                        </div>
                      </div>

                      <div>
                              
                        <input type="checkbox" id="agree1" value="checkedBox" v-model="checkbox">
                      </div>
                      <div>
                         
                        <input type="checkbox" id="agree2" value="checkedBox" v-model="checkbox">
                      </div>

                      <input type="submit" class="btn bg-gradient-dark" value="" style="margin-top: 5px">

                    </div>
                  </div>
                </form>
              </div>
            </div>
