<script>
import Navbar from "@/examples/PageLayout/Navbar.vue";
import AppFooter from "@/examples/PageLayout/Footer.vue";
import SoftInput from "@/components/SoftInput.vue";
import SoftCheckbox from "@/components/SoftCheckbox.vue";
import SoftButton from "@/components/SoftButton.vue";

import {mapMutations} from "vuex";

export default {
  name: "SignupBasic",
  data: function () {
    return {
      username: '',
      id: '',
      password: '',
      email: '',
      phoneNum: '',
      certification_num: '',
      msg: 'id   ', // id     .
      checkedBox: [],
      errorMsg: [],
      sampleData: '',
      isActive: false,
      hasError: false,
      class2: ['active', 'hasError'],
      show: false,
      mg_auth: this.$store.getters.getMg_auth,
      userRadio: true,
      comRadio: false,
      idBoxCheck: true, //id     id      
      idPassCheck: false, //id     ,  true  .
      phCertifPassCheck: false, //   ,  true
      passwordRule: /^(?=.*[a-zA-Z])(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{8,20}$/,  //   
      emailRule: /([\w-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([\w-]+\.)+))([a-zA-Z]{2,4}|[0-9]{1,3})(\]?)$/, //    
      //    
      com_name: '',
      com_phone: '',
      com_email: '',
      com_address: '',
      com_category: '',
      com_ced: '',
      com_businessRegistration: '',
      com_establishmentDate: ''
    }
  },
  components: {
    Navbar,
    AppFooter,
    SoftInput,
    SoftCheckbox,
    SoftButton,
  },
  created() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
  },
  beforeUnmount() {
    this.toggleEveryDisplay();
    this.toggleHideConfig();
  },
  methods: {
    ...mapMutations(["toggleEveryDisplay", "toggleHideConfig"]),
    // id      ,   
    idBoxNullCheck() {
      if (this.id !== '') {
        this.idBoxCheck = false
      } else if (this.id === '') {
        this.idBoxCheck = true
      }
    },
    //   
    sameIdCheck() {
      var self = this // axios  this       
      this.$axios.post('/jobfair/checkSameId', {user_id: this.id})
          .then(function (response) {
            console.log(response)
            self.idPassCheck = response.data
            if (self.idPassCheck === true) {
              self.msg = ' id '
            } else {
              self.msg = '   id '
            }
            alert(self.msg)
          })
          .catch(function (error) {
            console.log(error)
          })
    },
    //    (    )
    checkSubmit(e) {
      //  
      if (this.username === '') {
        this.$refs.nameBox.focus()
        e.preventDefault()
        return
      }
      //   
      if (this.idPassCheck === false) {
        alert(this.msg)
        this.$refs.idBox.focus()
        e.preventDefault()
        return
      }
      //password  
      if (this.password === '') {
        this.$refs.pwBox.focus()
        e.preventDefault()
        return
      }
      // password   -> 8~20 ,,  
      if (!this.passwordRule.test(this.password.trim())) {
        this.$refs.pwBox.focus()
        alert(' ,,  8~20  .')
        e.preventDefault()
        return
      }
      //email  
      if (this.email === '') {
        this.$refs.emailBox.focus()
        e.preventDefault()
        return
      }
      // email   
      if (!this.emailRule.test(this.email.trim())) {
        this.$refs.emailBox.focus()
        alert('   .')
        e.preventDefault()
        return
      }
      //   
      if (this.phoneNum === '') {
        this.$refs.phNumBox.focus()
        e.preventDefault()
        return
      }
      //   
      // if(this.phCertifPassCheck === false){
      //   this.$refs.certifNumBox.focus()
      //   alert('  ')
      //   e.preventDefault()
      //   return
      // }
      //  
      if (document.getElementById('agree1').checked === false) {
        alert("   .")
        this.errorMsg.push({msg: ' .'}) // 
        console.log(this.errorMsg)
        e.preventDefault()
        return
      }
      if (document.getElementById('agree2').checked === false) {
        alert("   .")
        this.errorMsg.push({msg: ' .'}) // 
        console.log(this.errorMsg)
        e.preventDefault()
      }
    },
    radioChange(e) {
      if (e.target.id === 'userType') this.show = false
      else this.show = true
    }
  }
};
</script>

<style scoped>
@import "./SignUp.css";
</style>
