/* eslint-disable */
<template>
  <div class="wrapBox">
    <div class="miniWrapBox">
      <div class="logoBox">
        <img src="#" alt="HomePage Logo">
      </div>
      <div class="radioBox">
        <input type="radio" name="radioBtn" value="1" v-model="auth"><span></span>
        <input type="radio" name="radioBtn" value="2" v-model="auth"><span></span>
      </div>
      <div class="loginBox">
        <input type="text" name="" id="idInput" placeholder="username" v-model="id"><br/>
        <input type="password" placeholder="password" v-model="pw"><br/>
        <button type="submit" id="loginBtn" @click="logInBtn"></button>
      </div>
      <div class="checkBoxWrap">
        <input type="checkbox">
        <span> </span><br/>
        <a href="#"></a> /
        <a href="#"></a>
        <p>  ?    !<router-link to="/joinView"></router-link></p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'MainView',
  data () {
    return {
      auth: this.$store.getters.getMg_auth,
      id: '',
      pw: ''

    }
  },
  updated() {
    this.$store.commit("setMg_auth", this.auth)
    console.log(this.auth)
    console.log(this.id)
    console.log(this.pw)
  },
  methods: {
    logInBtn () {
      this.$axios
        .post('/login', {
          auth: this.auth,
          id: this.id,
          pw: this.pw
        })
        .then((res) => {
          console.log(res.data)
          if(res.data == '1'){
            this.$router.push("/uMainView")
            // location.href='/uMainView'

          }else if(res.data == '2' || res.data == '3' ){
            this.$router.push("/cMainView")
            // location.href='/cMainView'

          }else if(res.data == '4' ){
            this.$router.push("/aMainView")
            // location.href='/aMainView'

          }else{
            alert(res.data)
          }
        })
        .catch((error) => {
          alert(' : '+error)
          this.isChecked=false;
          this.$router.push("/uMainView")

          // location.href='/uMainView'
        })
        .finally(() => {
          console.log('')
        })
    }
  }
}
</script>


